"use client"

import { useState, useEffect } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import Link from "next/link"
import type { Assignment, Submission } from "@/lib/db"

interface AssignmentWithSubmissions extends Assignment {
  submissions: Submission[];
}

export default function AssignmentDetailPage({ 
  params 
}: { 
  params: { id: string } 
}) {
  const [assignment, setAssignment] = useState<AssignmentWithSubmissions | null>(null)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const fetchAssignment = async () => {
      try {
        const response = await fetch(`/api/assignments/${params.id}`)
        if (!response.ok) {
          throw new Error('Failed to fetch assignment')
        }
        const data = await response.json()
        setAssignment(data)
      } catch (error) {
        console.error('Error fetching assignment:', error)
      } finally {
        setLoading(false)
      }
    }

    fetchAssignment()
  }, [params.id])

  if (loading) {
    return (
      <div className="p-8">
        <div className="animate-pulse">
          <div className="h-8 bg-gray-200 rounded w-1/4 mb-4"></div>
          <div className="h-4 bg-gray-200 rounded w-1/3 mb-8"></div>
          <Card className="p-6 mb-8">
            <div className="space-y-4">
              <div className="h-6 bg-gray-200 rounded w-1/3"></div>
              <div className="h-4 bg-gray-200 rounded w-3/4"></div>
              <div className="flex justify-between">
                <div className="h-4 bg-gray-200 rounded w-1/5"></div>
                <div className="h-4 bg-gray-200 rounded w-1/5"></div>
              </div>
            </div>
          </Card>
        </div>
      </div>
    )
  }

  if (!assignment) {
    return (
      <div className="p-8">
        <Card className="p-12 text-center">
          <p className="text-lg text-gray-500">Assignment not found</p>
          <Link href="/assignments" className="mt-4 inline-block">
            <Button variant="outline">Back to Assignments</Button>
          </Link>
        </Card>
      </div>
    )
  }

  return (
    <div className="p-8 max-w-4xl mx-auto">
      <div className="flex justify-between items-start mb-8">
        <div>
          <div className="flex items-center gap-3 mb-2">
            <Link href="/assignments">
              <Button variant="outline" size="sm">
                Back to Assignments
              </Button>
            </Link>
            <span className={`px-3 py-1 rounded-full text-sm font-medium ${
              assignment.status === "completed"
                ? "bg-green-100 text-green-800"
                : assignment.status === "in progress"
                ? "bg-blue-100 text-blue-800"
                : "bg-yellow-100 text-yellow-800"
            }`}>
              {assignment.status}
            </span>
          </div>
          <h1 className="text-2xl font-bold mb-2">{assignment.title}</h1>
          <p className="text-gray-600">{assignment.course_title}</p>
        </div>
        {assignment.status !== "completed" && (
          <Button>Submit Assignment</Button>
        )}
      </div>

      <div className="grid grid-cols-3 gap-6 mb-8">
        <Card className="p-4">
          <h3 className="text-sm font-medium text-gray-500 mb-1">Due Date</h3>
          <p className="text-lg font-semibold">
            {new Date(assignment.due_date).toLocaleDateString()}
          </p>
        </Card>
        <Card className="p-4">
          <h3 className="text-sm font-medium text-gray-500 mb-1">Points</h3>
          <p className="text-lg font-semibold">{assignment.points}</p>
        </Card>
        <Card className="p-4">
          <h3 className="text-sm font-medium text-gray-500 mb-1">Submissions</h3>
          <p className="text-lg font-semibold">{assignment.submissions?.length || 0}</p>
        </Card>
      </div>

      <div className="space-y-6">
        <Card className="p-6">
          <h2 className="text-lg font-semibold mb-4">Description</h2>
          <p className="text-gray-600 whitespace-pre-wrap">{assignment.description}</p>
        </Card>

        {assignment.submissions && assignment.submissions.length > 0 && (
          <Card className="p-6">
            <h2 className="text-lg font-semibold mb-4">Submission History</h2>
            <div className="space-y-4">
              {assignment.submissions.map((submission) => (
                <div 
                  key={submission.id} 
                  className="flex items-center justify-between py-3 border-b last:border-0"
                >
                  <div>
                    <p className="font-medium">{submission.student_name}</p>
                    <p className="text-sm text-gray-500">
                      Submitted: {new Date(submission.submitted_at).toLocaleString()}
                    </p>
                    <p className="text-sm text-gray-500">
                      Status: {submission.status}
                    </p>
                  </div>
                  <div className="text-right">
                    {submission.score && (
                      <p className="font-medium">Score: {submission.score}</p>
                    )}
                    {submission.feedback && (
                      <p className="text-sm text-gray-600">{submission.feedback}</p>
                    )}
                  </div>
                </div>
              ))}
            </div>
          </Card>
        )}
      </div>
    </div>
  )
}
